// REAL AI SERVICE: Backend API'ye istek atar
const generateSlideWithAI = async (prompt: string) => {
  const response = await fetch('/api/generate', {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ prompt })
  });

  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}));
    throw new Error(errorData.error || `AI HatasÄ±: ${response.status} - ${response.statusText}`);
  }

  return await response.json();
};

export default function PostFactoryPage() {
  const { slides, addSlide, activeSlideId, selectSlide, activeLanguage } = useEditorStore();
  
  // Zustand'Ä±n state'ini toplu gÃ¼ncellemek iÃ§in setState fonksiyonuna eriÅŸiyoruz
  const setState = useEditorStore.setState;
  const isLoaded = useRef(false);

  // AI Modal State
  const [showAiModal, setShowAiModal] = useState(false);
  const [prompt, setPrompt] = useState('');
  const [isGenerating, setIsGenerating] = useState(false);
  
  // Export State
  const [exportSlideId, setExportSlideId] = useState<string | null>(null);
  const [exportLanguage, setExportLanguage] = useState<string | null>(null);
  const [isExporting, setIsExporting] = useState(false);
  const exportRef = useRef<HTMLDivElement>(null);

  // Sayfa ilk yÃ¼klendiÄŸinde: Demo verisini al + LocalStorage'daki gÃ¶rsel ayarlarÄ± uygula
  useEffect(() => {
    const savedData = localStorage.getItem('kidsai-post-creator-v1');
    
    // Demo verisinin temiz bir kopyasÄ±nÄ± oluÅŸtur
    let initialSlides = JSON.parse(JSON.stringify(KIDS_APP_DEMO));

    if (savedData) {
      try {
        const parsed = JSON.parse(savedData);
        // Sadece gÃ¶rsel ayarlarÄ± (overrides) demo verisiyle birleÅŸtir
        if (parsed.overrides && Array.isArray(parsed.overrides)) {
          console.log("Local storage gÃ¶rsel ayarlarÄ± yÃ¼klendi.");
          
          initialSlides = initialSlides.map((slide: any) => {
            const saved = parsed.overrides.find((o: any) => o.id === slide.id);
            if (saved) {
              return {
                ...slide,
                phone: saved.phone || slide.phone, // KayÄ±tlÄ± telefon konumu
                image: saved.image || slide.image,  // KayÄ±tlÄ± resim
                elementTransforms: saved.elementTransforms || (slide as any).elementTransforms || {} // KayÄ±tlÄ± pozisyonlar
              };
            }
            return slide;
          });
        }
      } catch (e) {
        console.error("Local storage yÃ¼klenemedi:", e);
      }
    }

    // State'i baÅŸlat
    setState({ 
      slides: initialSlides, 
      activeSlideId: initialSlides[0].id 
    });
    
    // YÃ¼kleme tamamlandÄ± iÅŸaretini bir sonraki dÃ¶ngÃ¼ye bÄ±rakarak race condition'Ä± Ã¶nle
    setTimeout(() => {
      isLoaded.current = true;
    }, 100);
    
  }, []);

  // Sadece gÃ¶rsel ayarlarÄ± (Phone ve Image) kaydet, yazÄ±larÄ± kaydetme
  useEffect(() => {
    if (!isLoaded.current) return;

    // Sadece id, phone ve image verilerini filtrele
    const overrides = slides.map(s => ({
      id: s.id,
      phone: s.phone,
      image: s.image,
      elementTransforms: (s as any).elementTransforms || {}
    }));
    
    localStorage.setItem('kidsai-post-creator-v1', JSON.stringify({ overrides }));
  }, [slides]);

  // AI Generate Handler
  const handleAiGenerate = async () => {
    if (!prompt.trim()) return;
    
    setIsGenerating(true);
    try {
      const newSlide = await generateSlideWithAI(prompt);
      if (!newSlide.elementTransforms) newSlide.elementTransforms = {}; // Yeni slayt iÃ§in pozisyon verisini baÅŸlat
      
      // Mevcut slaytlarÄ±n Ã¼zerine yenisini ekle
      const currentSlides = useEditorStore.getState().slides;
      setState({ 
        slides: [...currentSlides, newSlide], // Sona ekle
        activeSlideId: newSlide.id // Yeni slaytÄ± seÃ§
      });
      
      setShowAiModal(false); // ModalÄ± kapat
      setPrompt('');
    } catch (error) {
      console.error("AI Generation Failed:", error);
    } finally {
      setIsGenerating(false);
    }
  };

  // Eleman gÃ¼ncelleme (Metin veya Pozisyon)
  const handleElementUpdate = (slideId: string, elementId: string, newText: string | undefined, newTransform: { x: number; y: number }) => {
    const currentSlides = useEditorStore.getState().slides;
    const updatedSlides = currentSlides.map(slide => {
      if (slide.id === slideId) {
        const updatedContent = newText ? { ...slide.content, [activeLanguage]: { ...slide.content[activeLanguage], [elementId]: newText } } : slide.content;
        const updatedTransforms = { ...(slide as any).elementTransforms, [elementId]: newTransform };
        return { ...slide, content: updatedContent, elementTransforms: updatedTransforms };
      }
      return slide;
    });
    setState({ slides: updatedSlides });
  };

  // --- Ä°NDÄ°RME FONKSÄ°YONLARI ---

  // Tekil Ä°ndirme (Aktif Slayt)
  const handleDownloadCurrent = async () => {
    if (isExporting || !activeSlideId) return;
    setIsExporting(true);
    setExportSlideId(activeSlideId); // Gizli container'a aktif slaytÄ± yÃ¼kle
    
    // Render olmasÄ± iÃ§in kÄ±sa bir sÃ¼re bekle
    setTimeout(async () => {
        if (exportRef.current) {
            try {
                // 2x Pixel Ratio ile yÃ¼ksek kalite Ã§Ä±ktÄ± al
                const dataUrl = await toPng(exportRef.current, { 
                    quality: 0.95, 
                    pixelRatio: 2,
                    cacheBust: true, // CORS sorunlarÄ±nÄ± Ã¶nlemek iÃ§in Ã¶nbelleÄŸi atla
                    skipAutoScale: true
                });
                saveAs(dataUrl, `kidsai-${activeSlideId}-${activeLanguage}.png`);
            } catch (err) {
                console.error("Export hatasÄ±:", err);
                alert("GÃ¶rsel oluÅŸturulurken bir hata oluÅŸtu.");
            }
        }
        setExportSlideId(null);
        setIsExporting(false);
    }, 500);
  };

  // Aktif Slayt - TÃ¼m Diller (ZIP)
  const handleDownloadCurrentAllLanguages = async () => {
    if (isExporting || !activeSlideId) return;
    setIsExporting(true);
    const zip = new JSZip();
    const languages = ['tr', 'en', 'de', 'es', 'fr', 'ja', 'ko', 'ar'];
    
    try {
        for (const lang of languages) {
            setExportLanguage(lang);
            setExportSlideId(activeSlideId);
            
            // Render bekleme
            await new Promise(resolve => setTimeout(resolve, 500));
            
            if (exportRef.current) {
                 try {
                     const dataUrl = await toPng(exportRef.current, { 
                        quality: 0.95, 
                        pixelRatio: 2,
                        cacheBust: true,
                        skipAutoScale: true
                     });
                     const base64Data = dataUrl.replace(/^data:image\/png;base64,/, "");
                     zip.file(`${activeSlideId}-${lang}.png`, base64Data, {base64: true});
                 } catch (innerErr) {
                     console.error(`Render hatasÄ± (${lang}):`, innerErr);
                 }
            }
        }
        
        const content = await zip.generateAsync({type:"blob"});
        saveAs(content, `kidsai-${activeSlideId}-all-languages.zip`);
    } catch (err) {
        console.error("Export hatasÄ±:", err);
        alert("Ä°ÅŸlem sÄ±rasÄ±nda bir hata oluÅŸtu.");
    } finally {
        setExportSlideId(null);
        setExportLanguage(null);
        setIsExporting(false);
    }
  };

  // Toplu Ä°ndirme (TÃ¼m Diller ve Slaytlar - ZIP)
  const handleDownloadAll = async () => {
    if (isExporting) return;
    setIsExporting(true);
    const zip = new JSZip();
    const languages = ['tr', 'en', 'de', 'es', 'fr', 'ja', 'ko', 'ar']; // TÃ¼m diller
    
    try {
        // 1. Dilleri DolaÅŸ
        for (const lang of languages) {
            setExportLanguage(lang);
            
            // 2. SlaytlarÄ± DolaÅŸ
            for (const slide of slides) {
                setExportSlideId(slide.id);
                
                // Render olmasÄ± iÃ§in bekle (GÃ¶rsellerin ve fontlarÄ±n yÃ¼klenmesi iÃ§in)
                await new Promise(resolve => setTimeout(resolve, 500));
                
                if (exportRef.current) {
                     try {
                         const dataUrl = await toPng(exportRef.current, { 
                            quality: 0.95, 
                            pixelRatio: 2,
                            cacheBust: true,
                            skipAutoScale: true
                         });
                         const base64Data = dataUrl.replace(/^data:image\/png;base64,/, "");
                         // KlasÃ¶r yapÄ±sÄ±: tr/slide-1.png
                         zip.folder(lang)?.file(`${slide.id}.png`, base64Data, {base64: true});
                     } catch (innerErr) {
                         console.error(`Slayt hatasÄ± (${lang} - ${slide.id}):`, innerErr);
                     }
                }
            }
        }
        
        const content = await zip.generateAsync({type:"blob"});
        saveAs(content, `kidsai-complete-set.zip`);
    } catch (err) {
        console.error("Genel export hatasÄ±:", err);
        alert("Toplu indirme sÄ±rasÄ±nda bir hata oluÅŸtu.");
    } finally {
        setExportSlideId(null);
        setExportLanguage(null);
        setIsExporting(false);
    }
  };

  return (
    <div className="flex w-full h-screen bg-slate-50 overflow-hidden font-sans text-slate-900">
      
      {/* SOL: Slayt Listesi */}
      <div className="w-24 bg-white border-r border-slate-200 flex flex-col items-center py-4 gap-4 z-20 overflow-y-auto custom-scrollbar">
        {slides.map((slide, index) => (
          <button
            key={slide.id}
            onClick={() => selectSlide(slide.id)}
            className={`w-16 h-16 rounded-xl border-2 transition-all overflow-hidden relative shrink-0 ${
              activeSlideId === slide.id ? 'border-blue-600 ring-2 ring-blue-100 scale-105' : 'border-slate-200 hover:border-slate-400'
            }`}
          >
            {/* KÃ¼Ã§Ã¼k Ã–nizleme - Performans iÃ§in scale kullanÄ±yoruz */}
            <div className="absolute inset-0 scale-[0.15] origin-top-left w-[1080px] h-[1350px] pointer-events-none bg-white">
                <SlideRenderer data={slide} lang={activeLanguage} isInteractive={false} />
            </div>
            <div className="absolute bottom-0 right-0 bg-black text-white text-[10px] px-1 font-bold">{index + 1}</div>
          </button>
        ))}
        
        <button 
            onClick={() => setShowAiModal(true)} 
            className="w-12 h-12 rounded-full bg-slate-100 hover:bg-slate-200 flex items-center justify-center text-slate-600 shrink-0 transition-colors"
            title="Yeni Slayt Ekle"
        >
            <Plus size={24} />
        </button>

        {/* Ä°ndirme ButonlarÄ± (Sol Panel AltÄ±) */}
        <div className="mt-auto flex flex-col gap-3 pb-6 border-t border-slate-200 pt-4 w-full items-center">
            <button 
                onClick={handleDownloadCurrent}
                disabled={isExporting}
                className="w-12 h-12 rounded-xl bg-white border border-slate-200 hover:border-blue-400 hover:text-blue-600 flex items-center justify-center text-slate-600 transition-all shadow-sm group relative"
                title={`Åu anki slaytÄ± indir (${activeLanguage.toUpperCase()})`}
            >
                {isExporting && exportSlideId === activeSlideId ? <Loader2 className="animate-spin" size={20} /> : <ImageIcon size={20} />}
                <span className="absolute left-14 bg-slate-800 text-white text-xs px-2 py-1 rounded opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap pointer-events-none z-50">
                    PNG Ä°ndir ({activeLanguage.toUpperCase()})
                </span>
            </button>

            <button 
                onClick={handleDownloadCurrentAllLanguages}
                disabled={isExporting}
                className="w-12 h-12 rounded-xl bg-white border border-slate-200 hover:border-purple-400 hover:text-purple-600 flex items-center justify-center text-slate-600 transition-all shadow-sm group relative"
                title="Bu SlaytÄ± TÃ¼m Dillerde Ä°ndir (ZIP)"
            >
                {isExporting && exportSlideId === activeSlideId && exportLanguage ? <Loader2 className="animate-spin" size={20} /> : <Globe size={20} />}
                <span className="absolute left-14 bg-slate-800 text-white text-xs px-2 py-1 rounded opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap pointer-events-none z-50">
                    Bu Slayt (TÃ¼m Diller ZIP)
                </span>
            </button>

            <button 
                onClick={handleDownloadAll}
                disabled={isExporting}
                className="w-12 h-12 rounded-xl bg-slate-900 hover:bg-slate-800 text-white flex items-center justify-center transition-all shadow-md group relative"
                title="TÃ¼m Dilleri ve SlaytlarÄ± Ä°ndir (ZIP)"
            >
                {isExporting && exportSlideId !== activeSlideId ? <Loader2 className="animate-spin" size={20} /> : <Archive size={20} />}
                <span className="absolute left-14 bg-slate-800 text-white text-xs px-2 py-1 rounded opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap pointer-events-none z-50">
                    TÃ¼m Diller ZIP
                </span>
            </button>
        </div>
      </div>

      {/* ORTA: Canvas (Sahne) */}
      <div className="flex-1 bg-slate-100 flex items-center justify-center p-8 overflow-hidden relative">
         {/* Arkaplan Grid Deseni */}
         <div className="absolute inset-0 opacity-[0.05] pointer-events-none" style={{ backgroundImage: 'radial-gradient(#000 1px, transparent 1px)', backgroundSize: '20px 20px' }} />
         
         {/* Canvas AlanÄ± */}
         <div className="w-full h-full flex items-center justify-center overflow-auto">
            {activeSlideId && (
              <div className="transform scale-[0.65] shadow-2xl shadow-slate-300 border border-slate-200 transition-all duration-300">
                  <SlideRenderer 
                    key={activeSlideId} // Ã–NEMLÄ°: Slayt deÄŸiÅŸtiÄŸinde bileÅŸeni zorla yenile (Re-mount)
                    data={slides.find(s => s.id === activeSlideId)!} 
                    lang={activeLanguage}
                    isInteractive={true}
                    onElementUpdate={(elId, txt, trans) => handleElementUpdate(activeSlideId!, elId, txt, trans)}
                  />
              </div>
            )}
         </div>
      </div>

      {/* SAÄ: EditÃ¶r Paneli */}
      <div className="z-20 shadow-xl relative h-full">
        <EditorPanel />
      </div>

      {/* AI PROMPT MODAL OVERLAY */}
      {showAiModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-slate-900/60 backdrop-blur-sm p-4 animate-in fade-in duration-300">
          <div className="bg-white rounded-2xl shadow-2xl w-full max-w-lg overflow-hidden relative flex flex-col">
            
            {/* Kapatma Butonu */}
            <button 
              onClick={() => setShowAiModal(false)}
              className="absolute top-4 right-4 text-slate-400 hover:text-slate-600 hover:bg-slate-100 p-1 rounded-full transition-colors"
            >
              <X size={24} />
            </button>

            <div className="p-8 flex flex-col items-center text-center">
              <div className="w-16 h-16 bg-gradient-to-tr from-blue-600 to-purple-600 rounded-2xl flex items-center justify-center text-white mb-6 shadow-lg shadow-blue-200 ring-4 ring-blue-50">
                <Sparkles size={32} />
              </div>
              
              <h2 className="text-2xl font-bold text-slate-800 mb-2">AI Post OluÅŸturucu</h2>
              <p className="text-slate-500 mb-8 text-sm leading-relaxed">
                Hayalindeki tasarÄ±mÄ± anlat, yapay zeka senin iÃ§in 7 dilde, gÃ¶rselleri ve yerleÅŸimiyle birlikte hazÄ±rlasÄ±n.
              </p>

              <div className="w-full relative mb-6">
                <textarea 
                  value={prompt}
                  onChange={(e) => setPrompt(e.target.value)}
                  placeholder="Ã–rn: Ã‡ocuklar iÃ§in orman hayvanlarÄ±nÄ± tanÄ±tan, yeÅŸil temalÄ±, eÄŸlenceli bir kart hazÄ±rla..."
                  className="w-full h-32 p-4 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none resize-none text-slate-700 placeholder:text-slate-400 text-sm"
                />
                <div className="absolute bottom-3 right-3 text-[10px] text-slate-400 font-medium uppercase tracking-wider">AI Powered</div>
              </div>

              <button 
                onClick={handleAiGenerate}
                disabled={isGenerating || !prompt.trim()}
                className="w-full py-4 bg-slate-900 hover:bg-slate-800 disabled:bg-slate-300 disabled:cursor-not-allowed text-white rounded-xl font-semibold transition-all flex items-center justify-center gap-2 shadow-xl shadow-slate-200"
              >
                {isGenerating ? (
                  <>
                    <Loader2 className="animate-spin" size={20} />
                    Sihir YapÄ±lÄ±yor...
                  </>
                ) : (
                  <>
                    <Sparkles size={20} />
                    TasarÄ±mÄ± OluÅŸtur
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* GÄ°ZLÄ° EXPORT CONTAINER (Ekran DÄ±ÅŸÄ±) */}
      {/* Ä°ndirme iÅŸlemi sÄ±rasÄ±nda slaytlar burada orijinal boyutunda (1080x1350) render edilir */}
      <div className="fixed left-[-9999px] top-0 pointer-events-none">
        <div ref={exportRef} className="w-[1080px] h-[1350px]">
            {exportSlideId && (
                <SlideRenderer 
                    data={slides.find(s => s.id === exportSlideId)!} 
                    lang={(exportLanguage as any) || activeLanguage} 
                    isInteractive={false} // DÃ¼zenleme modu kapalÄ± (temiz Ã§Ä±ktÄ±)
                />
            )}
        </div>
      </div>

    </div>
  );
}